@import url(https://fonts.googleapis.com/css?family=Muli&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.10.2/css/all.min.css);
:root {
  --background-color: #111025;
  --text-color: #fcfcfc;
  --generator-background-color: #181831;
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  color: #fff;
  display: flex;
  font-family: "Muli", sans-serif;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  min-height: 100vh;
}

p {
  margin: 5px 0;
}

h2 {
  margin: 10px 0 20px;
  text-align: center;
}

input[type="checkbox"] {
  margin-right: 0;
}

.container {
  background-color: var(--generator-background-color);
  padding: 20px;
  width: 350px;
  max-width: 100%;
  border-radius: 5px;
}

.result-container {
  background-color: #16162b;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  font-size: 14px;
  letter-spacing: 1px;
  padding: 12px 10px;
  height: 50px;
  width: 100%;
}

.result-container #result {
  word-wrap: break-word;
  max-width: calc(100% - 40px);
}

.result-container .btn {
  font-size: 20px;
  position: absolute;
  top: 5px;
  right: 5px;
  height: 40px;
  width: 40px;
}

.btn {
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  padding: 8px 12px;
  background-color: #45adff;
}

.btn-large {
  display: block;
  width: 100%;
  border-radius: 2px;
  font-weight: bold;
}

.setting {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 15px 0;
}

@media screen and (max-width: 400px) {
  .result-container {
    font-size: 14px;
  }
}

